<template>
  <div>
      <div class="py-4 px-2 shadow">
          <p class="mt-1 font-semibold text-lg float-right mr-3">Selfie</p>
          <button @click="$router.go(-1)" type="button"  class="btn btn-sm btn-dark-outline ml-2 rounded-circle px-2" style="border:1px solid black;">
              <span class="typcn typcn-arrow-left"></span>
          </button>
      </div>
      <div class="row">
        <div class="col-sm-12" v-if="!streamOn" style="height:100vh;" @click="getStream('video');streamOn=true;$forceUpdate()">
          <div style="height:90vh;" class="d-flex justify-content-center align-items-center shadow-lg rounded-lg">
              <div style="width:100%">
                  <p class="text-center text-4xl font-semibold">
                    Klik disini untuk Start Camera!
                  </p>
              </div>
          </div>
        </div>
        <div v-else class="col-sm-12 text-center">
          <video autoplay playsInline mutedstyle="height:60vh; width: 100vh;" id="videoQRCODE"></video>
           <button
              type="button"
              class="btn btn-sm btn-style13 text-xl mt-5"
              @click="getImage"
            >
              <span class="typcn typcn-camera"></span> Take Picture!
            </button>
          <canvas id="canvas" style="overflow: auto" class="d-none"></canvas>
        </div>
    </div>
  </div>
</template>
<script>
import axios from "axios";
import autophp from "@/plugins/auto";
let sdb = new autophp("ci");
export default {
  data() {
    return {
      isIos: false,
      countIos: 0,
      streamOn:false
    };
  },
  // 030398
  methods: {
    prosesAbsen(file) {
      let that = this;
      sdb.loadingOn();
      let user = localStorage.getItem("absenStatus");
      user = JSON.parse(user);
      let fd = new FormData();
      fd.append("id_user", user.data.nrp);
      fd.append("force", "true");
      fd.append("file", file);
      axios
        .post("https://wajah.officialapp.my.id/api/face/recognition", fd)
        .then(async (res) => {
          sdb.loadingOff();
          console.log("res", res);
          let data = res.data;
          console.log(data);
          if (data.status == false) {
            // wajah gagal terdeteksi
            alert("Wajah tidak ditemukan");
            // _showErrorDialog(message);
          } else {
            // berhasil wajah detect
            
            if (data.id_user != user.data.nrp) {
              // wajah bukan milik userid terkait
              alert("Wajah terdetect bukan milik user ini !");
            } else {
              // wajah milik user id
              if (Math.round(data.confidence) < 55) {
                // cek confidence data
                alert("Confidence data kurang dari 50%!");
              } else {
                // proses absen
                let timezone = user.data.timezone;
                if (that.$route.query.type == "IN") {
                  let time = await axios.get(
                    "https://wajah.officialapp.my.id/api/get/server_date"
                  );
                  let jam = parseInt(time.data.split(' ')[1].substring(0, 2));
                  time = time.data;
                  let temp1=time.split(' ')[0];
                  let temp2=time.split(' ')[1].split(':')[1];
                  let temp3=time.split(' ')[1].split(':')[2];
                  let temp4=':'+temp2+':'+temp3;
                  if (timezone == "WITA") {
                    jam = jam +1;
                    jam = jam.toString();
                    if(jam.length==1){
                      jam='0'+jam
                    }
                    temp4=temp1+' '+jam+temp4;
                
                  } else if (timezone == "WIT") {
                    jam = jam +2;
                    jam = jam.toString();
                    if(jam.length==1){
                      jam='0'+jam
                    }
                    temp4=temp1+' '+jam+temp4;
                 
                  }
                  var clockInTime = temp4;
                  var workingLocation = that.$route.query.target;
                  // alert(JSON.stringify({
                  //   nrp:data.id_user,
                  //   lat:that.$route.query.lat,
                  //   long:that.$route.query.long,
                  //   working_location:workingLocation,
                  //   clockInTime:clockInTime
                  // }));
                  let fd = new FormData();
                  fd.append("nrp", data.id_user);
                  fd.append("lat", that.$route.query.lat);
                  fd.append("long", that.$route.query.long);
                  fd.append("clock_in_time", clockInTime);
                  fd.append("working_location", workingLocation);
                  axios
                    .post("https://officialapp.my.id/api/bridge/clock_in", fd, {
                      "Content-Type": "multipart/form-data",
                    })
                    .then((res) => {
                      // alert(JSON.stringify(res.data));
                      sdb.alert("Absen Masuk Berhasil", "bg-green-400");
                      that.$router.push("/main");
                    });
                  alert("proses absen masuk ini " + data.id_user);
                } else {
                  let time = await axios.get(
                    "https://wajah.officialapp.my.id/api/get/server_date"
                  );
                  let jam = parseInt(time.data.split(' ')[1].substring(0, 2));
                  time = time.data;
                  let temp1=time.split(' ')[0];
                  let temp2=time.split(' ')[1].split(':')[1];
                  let temp3=time.split(' ')[1].split(':')[2];
                  let temp4=':'+temp2+':'+temp3;
                  if (timezone == "WITA") {
                    jam = jam +1;
                    jam = jam.toString();
                    if(jam.length==1){
                      jam='0'+jam
                    }
                    temp4=temp1+' '+jam+temp4;
                
                  } else if (timezone == "WIT") {
                    jam = jam +2;
                    jam = jam.toString();
                    if(jam.length==1){
                      jam='0'+jam
                    }
                    temp4=temp1+' '+jam+temp4;
                 
                  }
                  var clockOutTime = temp4;
                  //  alert(JSON.stringify({
                  //   nrp:data.id_user,
                  //   lat:that.$route.query.lat,
                  //   long:that.$route.query.long,
                  //   clockOutTime:clockOutTime
                  // }));
                  let fd = new FormData();
                  fd.append("nrp", data.id_user);
                  fd.append("lat", that.$route.query.lat);
                  fd.append("long", that.$route.query.long);
                  fd.append("clock_out_time", clockOutTime);
                  axios
                    .post(
                      "https://officialapp.my.id/api/bridge/clock_out",
                      fd,
                      { "Content-Type": "multipart/form-data" }
                    )
                    .then((res) => {
                      that.$router.push("/main");
                      sdb.alert("Absen Pulang Berhasil", "bg-green-400");
                    });
                  alert("proses absen keluar ini " + data.id_user);
                }
              }
            }
          }
        })
        .catch((err) => {
          sdb.loadingOff();
          alert(JSON.stringify(err));
        });
    },
    getImage() {
      let that = this;
      let video = document.getElementById("videoQRCODE");
      var canvas = document.getElementById("canvas");
      canvas.width = video.videoWidth;
      canvas.height = video.videoHeight;
      canvas
        .getContext("2d")
        .drawImage(video, 0, 0, video.videoWidth, video.videoHeight); // for drawing the video element on the canvas
      canvas.toBlob((blob) => {
        let file = new File([blob], "photo.jpg", { type: "image/jpeg" });
        that.prosesAbsen(file);
      }, "image/jpeg");
    },
    getStream(type) {
     
      if (
        !navigator.mediaDevices &&
        !navigator.getUserMedia &&
        !navigator.webkitGetUserMedia &&
        !navigator.mozGetUserMedia &&
        !navigator.msGetUserMedia
      ) {
        alert("User Media API not supported.");
        return;
      }

      var constraints = {};
      constraints[type] = true;

      this.getUserMedia(constraints)
        .then(function (stream) {
          var mediaControl = document.querySelector(type);

          if ("srcObject" in mediaControl) {
            mediaControl.srcObject = stream;
          } else if (navigator.mozGetUserMedia) {
            mediaControl.mozSrcObject = stream;
          } else {
            mediaControl.src = (window.URL || window.webkitURL).createObjectURL(
              stream
            );
          }

          mediaControl.play();
        })
        .catch(function (err) {
          alert("Error: " + err);
        });
    },
    getUserMedia(constraints) {
      // if Promise-based API is available, use it
      if (navigator.mediaDevices) {
        return navigator.mediaDevices.getUserMedia({
          video:{
            facingMode: "user", //'environment':'user',
          }
        });
      }

      // otherwise try falling back to old, possibly prefixed API...
      var legacyApi =
        navigator.getUserMedia ||
        navigator.webkitGetUserMedia ||
        navigator.mozGetUserMedia ||
        navigator.msGetUserMedia;

      if (legacyApi) {
        // ...and promisify it
        return new Promise(function (resolve, reject) {
          legacyApi.bind(navigator)(constraints, resolve, reject);
        });
      }
    },
    formatTgl(tgl, pattern = "YYYY-MM-DD") {
      return this.$datefns.format(new Date(tgl), pattern);
    },
  },
  mounted() {
    let that = this;
    if (localStorage.getItem("users")) {
    } else {
      this.$router.push("/");
    }
  },
};
</script>